body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
    text-decoration: none;
}
.container {
    max-width: 1246px;
    padding-left: 15px;
    padding-right: 15px;
    margin: 0 auto;
}

.topbar {
    background: #262831;
    color: #fff;
    padding: 30px 0;
}

.topbar a {
    color: #fff;
}

.header-nav {
    padding: 30px 0;
}

.header-nav .container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

.header-nav ul{
    display: -webkit-flex;
    display: flex;
}

.header-nav ul li{
    list-style: none;
    padding: 0 3px;
}

.header-nav ul li a {
    display: block;
    font-weight: 600;
    color: #444;
    outline: 0;
    opacity: 1;
    text-align: center;
    font-size: 14px;
    text-transform: uppercase;
    padding: 0 15px;
    letter-spacing: 0.3px;
    -webkit-transition: background .15s ease-in-out;
    transition: background .15s ease-in-out;
}

.header-nav ul li a:hover {
    color: #00c2e5;
}

.top-contactus {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

.top-contactus h4 {
    margin: 0 0 5px;
}
.services {
    text-align: center;
    padding: 50px 0;
    background: #f6f8f9;
}

.services h4 {
    font-size: 24px;
    line-height: 1.2;
    margin:0 0 10px;
}

.services .block-list {
    margin-top: 50px;
}
.services .block-list .col-content {
    position: relative;
    text-align: center;
    background-color: rgba(255,255,255,0.81);
    background-color: #fff\9;
